*{
    margin: 0;
    padding: 0;
    font-family: "Montserrat";
    /*background: #ccc;*/
    box-sizing: border-box;
}

html,body{
    width: 100%;
    height: 100%;
}

body{
    background: #ececec;
    overflow: hidden;
}
#loader{
    display: flex;
    background: #fff;
    left: 0;
    top: 0;
    justify-content: center;
    z-index: 9999;
    position: absolute;
    align-items: center;
    width: 100%;
    height: 100%;
    animation: anim2 1s cubic-bezier(0.19, 1, 0.22, 1) 7s both;
}

#wrapperload{
    width: 500px;
    height: 100px;
    background: #fff;
    overflow: hidden;
}

#wrapperload .elem{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    text-align: center;
}

#wrapperload .elem:nth-child(1){
    animation: anim 2s cubic-bezier(0.19, 1, 0.22, 1) 1s both;
}

#wrapperload .elem:nth-child(2){
    animation: anim 2s cubic-bezier(0.19, 1, 0.22, 1) 3s both;
}

#wrapperload .elem:nth-child(3){
    animation: anim 2s cubic-bezier(0.19, 1, 0.22, 1) 5s both;
}

/*#wrapperload .elem:nth-child(4){
    animation: anim 2s cubic-bezier(0.19, 1, 0.22, 1) 7s both;
}*/

@keyframes anim{
    to{
        margin-top: -100px;
    }
}

@keyframes anim2{
    to{
        transform: translateY(-100%);
    }
}



h1{
    text-transform: uppercase;
    letter-spacing: 15px;
    font-size: 12px;
    opacity: 0.8;
    font-weight: 800;
    margin-bottom: 20px;
}

#topBar {
    width: 100%;
    height: 0.5%;
    position: absolute;
    background: chocolate;
    animation: anim4 1s cubic-bezier(0.19, 1, 0.22, 1) 9s both;
}

@keyframes anim4 {
    to{
        background-color: rgb(46, 59, 95);
        opacity: 1;
    }
}

#bg{
    width: 100%;
    height: 100vh;
    transition: all cubic-bezier(0.19, 1, 0.22, 1) .5s;
    background: #fff;
}

nav{
    display: flex;
    width: 100%;
    height: 80px;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    overflow: hidden;
}

nav img{
    width: 110px;
}

nav #links{
    display: flex;
    align-items: center;
}

nav #links a{
    margin-left: 30px;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 11px;
    color: #000;
    font-weight: 700;
    letter-spacing: 4px;
    opacity: 0.7;
}

nav #links a:nth-child(1) {
    opacity: 0.7;
    animation: active 1s cubic-bezier(0.19, 1, 0.22, 1) 10s both;
}

@keyframes active{
    to{
        font-weight: bolder;
        font-size: medium;
        opacity: 0.5;
    }
}

.coverimage{
    width: 100%;
    height: 100%;
    top: 0.5%;
    left: 0%;
    right: 0%;
    bottom: 0%;
    position: absolute;
    animation: anim3 2s cubic-bezier(0.19, 1, 0.22, 1) 8s both;
}

@media screen and (min-width:847px){
    .coverimage{
        width: 50%;
        height: 100%;
        top: 0.5%;
        left: 0%;
        right: 0%;
        bottom: 0%;
        position: absolute;
        animation: anim3 2s cubic-bezier(0.19, 1, 0.22, 1) 8s both;
    }

    p{
        top: 8%;
        bottom: 20%;
        left: 54%;
        right: 2%;
        position: absolute;
        color: rgb(41, 54, 97);
        font-size: 54px;
        background: bisque;
    }

    #bg{
        top: 1%;
        bottom: 80%;
        left: 50%;
        right: 2%;
        position: absolute;
        transition: all cubic-bezier(0.19, 1, 0.22, 1) .5s;
        background: #fff;
        width: 50%;
    }

    nav{
        display: flex;
        width: 100%;
        height: 80px;
        justify-content: flex-start;
        overflow: hidden;
        align-items: center;
        margin: 0 auto;
        overflow: hidden;
    }



    .explorebtn{
        display: none;
    }

    .btn{
        display: visible;
        top: 74%;
        left: 64%;
        right: 2%;
        bottom: 16%;
        position: absolute;
        background: coral;
        padding: 10px;
        font-weight: bolder;
        font-size: 25px;
        color: cornsilk;
        width: 24%;
        height: auto;
        border-radius: 15px;
        border: 2px solid coral;
        animation: btncoloranim 2s cubic-bezier(0.19, 1, 0.22, 1) 10.3s both;
    }

    footer{
        top: 94%;
        display: flex;
        bottom: 0;
        left: 50%;
        right: 0;
        position: absolute;
        justify-content: center;
        align-items: center;
        background-color: rgb(46, 59, 95);
        color: beige;
        animation: footeranim 1s cubic-bezier(0.19, 1, 0.22, 1) 13s both;
    }
    
    
}



@keyframes anim3{
    to{
        transform: rotate3d(25deg,25deg,45deg);
        transition: all cubic-bezier(0.19, 1, 0.22, 1) 1s;
    }
}

#introhead{
    top: 30%;
    bottom: 30%;
    left: 20%;
    right: 20%;
    overflow: hidden;
    font-size: 45px;
    font-weight: bolder;
    color: #fff;
    position: absolute;
    justify-content: center;
    align-items: center;
    animation: anim5 1s cubic-bezier(0.19, 1, 0.22, 1) 9s both;
}

@keyframes anim5{
    to{
        transform: scaleY(1.24);
        transform: scaleX(1.1);;
    }
}

#explorebtn{
    top: 60%;
    left: 0;
    right: 0;
    bottom: 10%;
    position: absolute;
    width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
}

.explorebtn{
    background: coral;
    padding: 10px;
    font-weight: bolder;
    font-size: 25px;
    color: cornsilk;
    width: 50%;
    border-radius: 15px;
    border: 2px solid coral;
    animation: btncoloranim 2s cubic-bezier(0.19, 1, 0.22, 1) 10.3s both;
}

@keyframes btncoloranim {
    to{
        background: rgb(46, 59, 95);
        border: 2px solid rgb(46, 59, 95);
    }
}

#footer{
    top: 90%;
    display: flex;
    bottom: 0;
    left: 0;
    right: 0;
    position: absolute;
    justify-content: center;
    align-items: center;
    background-color: rgb(46, 59, 95);
    color: beige;
    animation: footeranim 1s cubic-bezier(0.19, 1, 0.22, 1) 13s both;
}

a{
    text-decoration: none;
    color: rgb(141, 163, 224);
}

@keyframes footeranim {
    to{
        background: coral;
    }
}